/*Slider*/

.pokemon_slider_container{
    width: 90%;
    max-width: 950px;
    height: 460px;
    margin: 0 auto;
    position: relative;
  }
  
  .pokemon_slider_container .random{
    position: absolute;
    z-index: 100;
    left: 100px;
    top: 0;
    padding: 5px 10px;
    border: none;
    border-radius: 5px;
    outline: none;
    background-color: #fffb00;
  }
  
  .pokemon_slider_list{
    height: 100%;
    list-style: none;
    display: -webkit-flex;
    display: flex;
    position: relative;
  }
  
  .pokemon_slider_item{
    z-index: 18;
    transition: .3s;
    position: absolute;
    right: 50%;
    top: 50%;
    -webkit-transform: translateX(50%) translateY(-50%);
            transform: translateX(50%) translateY(-50%);
    opacity: 0;
  }
  
  .pokemon_slider_item.active{
    -webkit-transform: scale(1.2) translateX(40%) translateY(-40%);
            transform: scale(1.2) translateX(40%) translateY(-40%);
    z-index: 20;
    opacity: 1;
  }
  
  .pokemon_slider_item.prev{
    z-index: 19;
    right: 80%;
    opacity: 0.9;
  }
  
  .pokemon_slider_item.next{
    z-index: 19;
    right: 21%;
    opacity: 0.9;
  }
  
